import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'white'} external={external}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class GamesCardPage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'blue'}
        withBackButton
        zIndex={10}
        title="Karetní"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'blue'}>
              <Wrapper>
                <CircleLink
                  to="https://www.youtube.com/embed/055kC3FYxK0"
                  img={'./games/card/bang.jpg'}
                  text="Bang!"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://www.youtube.com/embed/jNbWNfxVMow"
                  img={'./games/card/6bere.jpg'}
                  text="6 Bere"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://www.youtube.com/embed/9nlKaqVXfh4"
                  img={'./games/card/unikovka.jpg'}
                  text="Únikovka"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



