import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'blue'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'blue'} external={external}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class SeasonsPage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'yellow'}
        urlMatch={'seasons/'}
        withCloseButton
        zIndex={5}
        title="Sezóny"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'blue'}>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/vanoce?e=23867307/55895255"
                  img={'./seasons/vanoce.jpg'}
                  text="Vánoce"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/prvy_nastrel_960x1500?e=23867307/63682785"
                  img={'./seasons/valentyn.jpg'}
                  text="Valentýn"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/svatba_vizual_dve?e=23867307/63683052"
                  img={'./seasons/svatba.jpg'}
                  text="Svatba"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



