import configureStore from './configureStore';
import { createHashHistory } from 'history';
import React, { Component } from 'react';
import { ConnectedRouter } from 'react-router-redux';
import { Provider } from 'react-redux';
import { Route } from 'react-router';
import routeList from './routeList';
import RouteAnimated from './components/RouteAnimated/RouteAnimated.react';

import HomePage from './routes/HomePage/HomePage.react';
import GamesPage from './routes/GamesPage/GamesPage.react';
import GamesKnowledgePage from './routes/GamesKnowledgePage/GamesKnowledgePage.react';
import GamesCardPage from './routes/GamesCardPage/GamesCardPage.react';
import GamesQuizPage from './routes/GamesQuizPage/GamesQuizPage.react';
import GamesFamilyPage from './routes/GamesFamilyPage/GamesFamilyPage.react';
import GamesPartyPage from './routes/GamesPartyPage/GamesPartyPage.react';
import GamesAdultsPage from './routes/GamesAdultsPage/GamesAdultsPage.react';
import GamesStrategyPage from './routes/GamesStrategyPage/GamesStrategyPage.react';
import GamesBrainTeaserPage from './routes/GamesBrainTeaserPage/GamesBrainTeaserPage.react';
import EducationPage from './routes/EducationPage/EducationPage.react';
import CreativityPage from './routes/CreativityPage/CreativityPage.react';
import OfficePage from './routes/OfficePage/OfficePage.react';
import WishesPage from './routes/WishesPage/WishesPage.react';
import PackingPage from './routes/PackingPage/PackingPage.react';
import LicensePage from './routes/LicensePage/LicensePage.react';
import DesignGiftsPage from './routes/DesignGiftsPage/DesignGiftsPage.react';
import NameConceptsPage from './routes/NameConceptsPage/NameConceptsPage.react';
import SeasonsPage from './routes/SeasonsPage/SeasonsPage.react';

const history = createHashHistory();
const store = configureStore(history);

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <ConnectedRouter history={history}>
          <div>
            <Route path={routeList.homepage} component={HomePage} />

            <RouteAnimated path={routeList.games} component={GamesPage} />
            <RouteAnimated path={routeList.gamesKnowledge} component={GamesKnowledgePage} />
            <RouteAnimated path={routeList.gamesCard} component={GamesCardPage} />
            <RouteAnimated path={routeList.gamesQuiz} component={GamesQuizPage} />
            <RouteAnimated path={routeList.gamesFamily} component={GamesFamilyPage} />
            <RouteAnimated path={routeList.gamesParty} component={GamesPartyPage} />
            <RouteAnimated path={routeList.gamesAdult} component={GamesAdultsPage} />
            <RouteAnimated path={routeList.gamesStrategy} component={GamesStrategyPage} />
            <RouteAnimated path={routeList.gamesBrainTeaser} component={GamesBrainTeaserPage} />

            <RouteAnimated path={routeList.education} component={EducationPage} />

            <RouteAnimated path={routeList.creativity} component={CreativityPage} />

            <RouteAnimated path={routeList.office} component={OfficePage} />

            <RouteAnimated path={routeList.wishes} component={WishesPage} />

            <RouteAnimated path={routeList.packing} component={PackingPage} />

            <RouteAnimated path={routeList.license} component={LicensePage} />

            <RouteAnimated path={routeList.designGifts} component={DesignGiftsPage} />

            <RouteAnimated path={routeList.nameConcepts} component={NameConceptsPage} />

            <RouteAnimated path={routeList.seasons} component={SeasonsPage} />
          </div>
        </ConnectedRouter>
      </Provider>
    );
  }
}

export default App;



