import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'white'} external={external}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class NameConceptsPage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'blue'}
        urlMatch={'nameConcepts/'}
        withCloseButton
        zIndex={5}
        title="Jmenné koncepty"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'white'}>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/hrnky?e=23867307/63682199"
                  img={'./nameConcepts/hrnky.jpg'}
                  text="Hrnky"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/zip__ci?e=23867307/63682377"
                  img={'./nameConcepts/zipacek.jpg'}
                  text="Zipáček"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/ta_ky_do_kabelky?e=23867307/63682335"
                  img={'./nameConcepts/taskyDoKabelky.jpg'}
                  text="Tašky do kabelky"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



