import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} external={external} color={'white'}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class GamesKnowledgePage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'blue'}
        withBackButton
        zIndex={10}
        title="Vědomostní"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'blue'}>
              <Wrapper>
                <CircleLink
                  to="http://www.youtube.com/watch_popup?v=EJqn1CFDPok"
                  img={'./games/knowledge/cortex.jpg'}
                  text="Cortex"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="http://www.youtube.com/watch_popup?v=dRYvHWLVueY"
                  img={'./games/knowledge/brainbox.jpg'}
                  text="Brainbox"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



