import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'white'} external={external}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class OfficePage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'pink'}
        urlMatch={'office/'}
        withCloseButton
        zIndex={5}
        title="Kancelář"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'white'}>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/bloky_chlup_a_flitr_pl?e=23867307/63681813"
                  img={'./office/bloky.jpg'}
                  text="Bloky"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/magnetky_a_lep_tka_pl?e=23867307/63681846"
                  img={'./office/lepitkaAMagnetky.jpg'}
                  text="Lepítka a magnetky"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/pouzdra_na_dokumenty?e=23867307/63681860"
                  img={'./office/pouzdraNaDokumenty.jpg'}
                  text="Pouzdra na dokumenty"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/designov__propisky_pl?e=23867307/63681917"
                  img={'./office/psaciPotreby.jpg'}
                  text="Psací potřeby"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



