import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'blue'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'blue'} external={external}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class CreativityPage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'yellow'}
        urlMatch={'creativity/'}
        withCloseButton
        zIndex={5}
        title="Kreativita"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'blue'}>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/crayola_prezentace_podrobnejsi_2018?e=23867307/63366460"
                  img={'./creativity/crayola.jpg'}
                  text="Crayola"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/experiment_ln__sady?e=23867307/63681128"
                  img={'./creativity/experimentalniSady.jpg'}
                  text="Experimentální sady"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



