import React from 'react';
import { AnimatedRoute } from 'react-router-transition';
import RPT from 'prop-types';
import { withRouter } from 'react-router';

@withRouter
export default class RouteAnimated extends React.PureComponent {
  static propTypes = {
    path: RPT.string.isRequired,
    component: RPT.node.isRequired
  };

  render() {
    const { path, component } = this.props;
    return (
      <AnimatedRoute
        path={path}
        component={component}
        atEnter={{ offset: 100 }}
        atLeave={{ offset: 100 }}
        atActive={{ offset: 0 }}
        mapStyles={styles => ({
          transform: `translateX(${styles.offset}%)`
        })}
      />
    );
  }
}



