import mergeClassNames from '../../lib/mergeClassNames';
import React, { PureComponent } from 'react';
import style from './LayoutHomePage.pcss';
import { withRouter } from 'react-router';
import routeList from '../../routeList';

@withRouter
export default class LayoutHomePage extends PureComponent {
  isPageOpen = () => {
    const {
      location: { pathname },
    } = this.props;

    return pathname !== routeList.homepage;
  };

  render() {
    const { children } = this.props;

    return (
      <div
        className={mergeClassNames([
          style.wrapper,
          this.isPageOpen() && style.isOpen,
        ])}
      >
        <div className={style.childrenWrapper}>{children}</div>
      </div>
    );
  }
}



