import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'white'}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class GamesAdultsPage extends PureComponent {
  render() {
    return (
      <LayoutPage color={'blue'} withBackButton zIndex={10} title="Pro dospělé">
        <Container>
          <Column>
            <CircleLink
              img={'./games/adults/nevinneHratkyDeluxe.jpg'}
              text="Nevinné hrátky Deluxe"
            />
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



