import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'white'} external={external}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class DesignGiftsPage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'pink'}
        urlMatch={'designGifts/'}
        withCloseButton
        zIndex={5}
        title="Designové dárky"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'white'}>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/katalog_original?e=23867307/63659541"
                  img={'./designGifts/albiKolekce.jpg'}
                  text="Albi kolekce"
                  external
                />
                <CircleLink
                  to="https://issuu.com/albicr/docs/oblecenie?e=23867307/63682647"
                  img={'./designGifts/obleceni.jpg'}
                  text="Oblečení"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/hrnky?e=23867307/63682199"
                  img={'./designGifts/hrnky.jpg'}
                  text="Hrnky"
                  external
                />
                <CircleLink
                  to="https://issuu.com/albicr/docs/pol_t__e?e=23867307/63682400"
                  img={'./designGifts/polstare.jpg'}
                  text="Polštáře"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/m_j_bar?e=23867307/63682256"
                  img={'./designGifts/mujBar.jpg'}
                  text="Můj Bar"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/jubileum?e=23867307/63682576"
                  img={'./designGifts/jubileum.jpg'}
                  text="Jubileum"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



