import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'blue'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'blue'} external>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class LicensePage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'yellow'}
        urlMatch={'license/'}
        withCloseButton
        zIndex={5}
        title="Licence"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'blue'}>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/heart_home_prezentace?e=23867307/63681106"
                  img={'./license/heartAndHome.jpg'}
                  text="Heart & Home"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/_mty_katalog_cel_?e=23867307/63681245"
                  img={'./license/meToYou.jpg'}
                  text="Me To You"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/hrejivi_plysaci?e=23867307/61694599"
                  img={'./license/hrejivaPlys.jpg'}
                  text="Hřejivá plyš"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



