import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage
        img={getImageUrl(img)}
        to={to}
        color={'white'}
        external={external}
      >
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class GamesBrainTeaserPage extends PureComponent {
  render() {
    return (
      <LayoutPage color={'blue'} withBackButton zIndex={10} title="Hlavolamy">
        <Container>
          <Column>
            <CircleLink
              to="https://issuu.com/albicr/docs/huzzle_hlavolamy_?e=23867307/63680387"
              img={'./games/brainTeaser/huzzle.jpg'}
              text="Huzzle"
              external
            />
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



