import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';
import routeList from '../../routeList';

const CircleLink = ({ img, to, text }) => {
  return (
    <CircleWrapper isTransparent color={'blue'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'blue'}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class GamesPage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'yellow'}
        urlMatch={'games/'}
        withCloseButton
        zIndex={5}
        title="Hry"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'blue'}>
              <Wrapper>
                <CircleLink
                  img={'./games/knowledge.jpg'}
                  to={routeList.gamesKnowledge}
                  text="Vědomostní"
                />
                <CircleLink
                  img={'./games/party.jpg'}
                  to={routeList.gamesParty}
                  text="Party"
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  img={'./games/cards.jpg'}
                  to={routeList.gamesCard}
                  text="Karetní"
                />
                <CircleLink
                  img={'./games/adults.jpg'}
                  to={routeList.gamesAdult}
                  text="Pro dospělé"
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  img={'./games/quiz.jpg'}
                  to={routeList.gamesQuiz}
                  text="Kvízové"
                />
                <CircleLink
                  img={'./games/strategy.jpg'}
                  to={routeList.gamesStrategy}
                  text="Strategické"
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  img={'./games/family.jpg'}
                  to={routeList.gamesFamily}
                  text="Rodinné"
                />
                <CircleLink
                  img={'./games/brainTeaser.jpg'}
                  to={routeList.gamesBrainTeaser}
                  text="Hlavolamy"
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



