const convertAlbiNameToHex = (colorName) => {
  const colorPalette = {
    blue: '#00294E',
    lighBlue: '#27bed5',
    pink: '#e30d5c',
    white: '#ffffff',
    yellwo: '#fede3c',
  }

  return colorPalette[colorName];
}

export default convertAlbiNameToHex;


