import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import DecoratorWrapper from '../../components/DecoratorWrapper/DecoratorWrapper.react';
import LayoutPage from '../../components/LayoutPage/LayoutPage.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';

const CircleLink = ({ img, to, text, external }) => {
  return (
    <CircleWrapper isTransparent color={'white'}>
      <CircleLinkWithImage img={getImageUrl(img)} to={to} color={'white'} external={external}>
        {text}
      </CircleLinkWithImage>
    </CircleWrapper>
  );
};

export default class WishesPage extends PureComponent {
  render() {
    return (
      <LayoutPage
        color={'lightBlue'}
        urlMatch={'wishes/'}
        withCloseButton
        zIndex={5}
        title="Přání do obálky"
      >
        <Container>
          <Column>
            <DecoratorWrapper isTransparent color={'white'}>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/prani_trosku_jinak__1_?e=23867307/63682034"
                  img={'./wishes/klasicka.jpg'}
                  text="Klasická"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/prani_trosku_jinak__1_?e=23867307/63682034"
                  img={'./wishes/hraci.jpg'}
                  text="Hrací"
                  external
                />
              </Wrapper>
              <Wrapper>
                <CircleLink
                  to="https://issuu.com/albicr/docs/prani_trosku_jinak__1_?e=23867307/63682034"
                  img={'./wishes/obalkyNaPenize.jpg'}
                  text="Obálky na peníze"
                  external
                />
              </Wrapper>
            </DecoratorWrapper>
          </Column>
        </Container>
      </LayoutPage>
    );
  }
}



