import CloseButton from '../CloseButton/CloseButton.react';
import mergeClassNames from '../../lib/mergeClassNames';
import React, { PureComponent } from 'react';
import style from './LayoutPage.pcss';
import Title from '../Title/Title.react';
import { withRouter } from 'react-router';

@withRouter
export default class LayoutPage extends PureComponent {
  getIconColor = color => {
    const colorPalette = {
      white: 'blue',
      yellow: 'blue'
    };

    return colorPalette[color] || 'white';
  };

  isPageOpen = () => {
    const { urlMatch, location: { pathname } } = this.props;

    return ~pathname.indexOf(urlMatch);
  };

  render() {
    const { children, color, withCloseButton, withBackButton, title } = this.props;

    return (
      <div
        className={mergeClassNames([
          style.wrapper,
          this.isPageOpen() && style.isOpen,
          style[`background-${color}`]
        ])}
      >
        {(withCloseButton || withBackButton) && (
          <CloseButton
            color={this.getIconColor(color)}
            useBackIcon={withBackButton}
          />
        )}
        <Title color={color}>{title}</Title>
        <div className={style.childrenWrapper}>{children}</div>
      </div>
    );
  }
}



