import createCloseIcon from './createCloseIcon';
import createBackIcon from './createBackIcon';
import mergeClassNames from '../../lib/mergeClassNames';
import React, { PureComponent } from 'react';
import style from './CloseButton.pcss';
import { withRouter } from 'react-router';

@withRouter
export default class CloseButton extends PureComponent {
  handleClose = () => {
    const { history } = this.props;

    history.push(history.location.pathname.split('/').slice(0, -1).join('/') || '/');
  };

  render() {
    const { color, useBackIcon } = this.props;
      
    return (
      <div className={style.wrapper}>
        <div onClick={this.handleClose} className={style.button}>
          {useBackIcon
          ? createBackIcon(color, style.icon)
          : createCloseIcon(color, style.icon)
          }
          <div
            className={mergeClassNames([
              style.hoverDecorator,
              style[`color-${color}`]
            ])}
          />
        </div>
      </div>
    );
  }
}



