import CircleLinkWithImage from '../../components/CircleLinkWithImage/CircleLinkWithImage.react';
import CircleWrapper from '../../components/CircleWrapper/CircleWrapper.react';
import LayoutHomePage from '../../components/LayoutHomePage/LayoutHomePage.react';
import Logo from '../../components/Logo/Logo.react';
import React, { PureComponent } from 'react';
import { Column, Container, Wrapper } from '../../components/Grid';
import { getImageUrl } from '../../lib/image';
import routeList from '../../routeList';

export default class HomePage extends PureComponent {
  render() {
    return (
      <LayoutHomePage>
        <Logo />
        <Container>
          <Column>
            <Wrapper>
              <CircleWrapper color={'yellow'}>
                <CircleLinkWithImage
                  img={getImageUrl('./games/cover.jpg')}
                  to={routeList.games}
                  color={'yellow'}
                >
                  Hry
                </CircleLinkWithImage>
                <CircleLinkWithImage
                  img={getImageUrl('./education/cover.jpg')}
                  to={routeList.education}
                  color={'yellow'}
                >
                  Vzdělávání
                </CircleLinkWithImage>
                <CircleLinkWithImage
                  img={getImageUrl('./creativity/cover.jpg')}
                  to={routeList.creativity}
                  color={'yellow'}
                >
                  Kreativita
                </CircleLinkWithImage>
              </CircleWrapper>
            </Wrapper>
            <Wrapper>
              <CircleWrapper color={'pink'}>
                <CircleLinkWithImage
                  img={getImageUrl('./diary/cover.jpg')}
                  to="https://issuu.com/albicr/docs/21582_pl_cz?e=23867307/63659617"
                  color={'pink'}
                  external
                >
                  Diáře
                </CircleLinkWithImage>
                <CircleLinkWithImage
                  img={getImageUrl('./office/cover.jpg')}
                  to={routeList.office}
                  color={'pink'}
                >
                  Kancelář
                </CircleLinkWithImage>
              </CircleWrapper>
              <CircleWrapper color={'lightBlue'}>
                <CircleLinkWithImage
                  img={getImageUrl('./wishes/cover.jpg')}
                  to={routeList.wishes}
                  color={'lightBlue'}
                >
                  Přání do obálky
                </CircleLinkWithImage>
              </CircleWrapper>
            </Wrapper>
            <Wrapper>
              <CircleWrapper color={'white'}>
                <CircleLinkWithImage
                  img={getImageUrl('./packing/cover.jpg')}
                  to={routeList.packing}
                  color={'white'}
                >
                  Balení dárků
                </CircleLinkWithImage>
              </CircleWrapper>
              <CircleWrapper color={'yellow'}>
                <CircleLinkWithImage
                  img={getImageUrl('./license/cover.jpg')}
                  to={routeList.license}
                  color={'yellow'}
                >
                  Licence
                </CircleLinkWithImage>
              </CircleWrapper>
              <CircleWrapper color={'pink'}>
                <CircleLinkWithImage
                  img={getImageUrl('./designGifts/cover.jpg')}
                  to={routeList.designGifts}
                  color={'pink'}
                >
                  Designové dárky
                </CircleLinkWithImage>
              </CircleWrapper>
            </Wrapper>
            <Wrapper>
              <CircleWrapper color={'blue'}>
                <CircleLinkWithImage
                  img={getImageUrl('./nameConcepts/cover.jpg')}
                  to={routeList.nameConcepts}
                  color={'blue'}
                >
                  Jmenné koncepty
                </CircleLinkWithImage>
              </CircleWrapper>
              <CircleWrapper color={'lightBlue'}>
                <CircleLinkWithImage
                  img={getImageUrl('./funnyGifts/cover.jpg')}
                  // to={routeList.funnyGifts}
                  color={'lightBlue'}
                >
                  Humorné dárky
                </CircleLinkWithImage>
              </CircleWrapper>
              <CircleWrapper color={'yellow'}>
                <CircleLinkWithImage
                  img={getImageUrl('./seasons/cover.jpg')}
                  to={routeList.seasons}
                  color={'yellow'}
                >
                  Sezóny
                </CircleLinkWithImage>
              </CircleWrapper>
            </Wrapper>
          </Column>
        </Container>
      </LayoutHomePage>
    );
  }
}



